.App {
  text-align: center;
}
@font-face {
  font-family: "Montserrat";
  src: url("../css/fonts/Montserrat-Regular.ttf") format("truetype");
  font-weight: 400; /* Нормальный вес (обычный) */
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("../css/fonts/Montserrat-Bold.ttf") format("truetype");
  font-weight: 700; /* Жирный вес */
  font-style: normal;
}

@font-face {
  font-family: "Montserrat";
  src: url("../css/fonts/Montserrat-Light.ttf") format("truetype");
  font-weight: 300; /* Легкий вес */
  font-style: normal;
}

*{
