.video-grid {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}




.video-block {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.video-container {
  position: relative;
  flex: 1;
  max-width: 300px; /* Максимальная ширина, чтобы ограничить размер */
  aspect-ratio: 16 / 9; /* Соотношение сторон: 16:9 */
  background-color: #000; /* Задний фон, если видео отсутствует */
  display: flex;
  justify-content: center;
  align-items: center;
  border: 1px solid #ccc;
  border-radius: 8px;
  overflow: hidden;
  transition: transform 0.3s ease, max-width 0.3s ease;
}

.video-container video {
  width: 100%; /* Видео займет всю ширину контейнера */
  height: 100%; /* Видео адаптируется по высоте контейнера */
  object-fit: cover; /* Покрывает весь контейнер, обрезая излишки, если нужно */
}

.video-container.enlarged {
  max-width: 700px;

z-index: 10;
}





